<template>
  <div class="page-sugar">
    <section class="pass count-card">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="card-intro clearfix">
          <span>7天血糖值达标比例</span>(总计{{sugarControl.GoodControlTotalAmount7Day}}次测量)
          <!-- <span>详情 <icon name="question-circle-o"></icon></span> -->
        </p>
        <p class="count-percent clearfix">
          <span>{{Math.round(sugarControl.GoodControlRate7Day)}}<i class="percent-icon">%</i></span>
          <span></span>
        </p>
      </div>
      <ul class="card-content">
        <li class="content-row" v-for="(item, index) in passRate" :key="index">
          <span class="time">{{timeRanges[index]}}</span>
          <p class="percent">
            <span class="percent-line" :style="{ width: (item * 0.7) + '%' }">
              <i />
            </span>
            <span class="percent-text">{{item + '%'}}</span>
          </p>
        </li>
      </ul>
    </section>
    <section class="count-card no-content">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="card-intro clearfix">
          <span>7天测量≥3次的新入组患者比例</span>
          <span class="explain" @click="togglePop">说明 <icon name="question-circle-o"></icon></span>
        </p>
        <div class="count-percent clearfix">
          <span>{{Math.round(sugarControl.NewPatientTestRate7Day)}}<i class="percent-icon">%</i></span>
          <span>
          </span>
        </div>
      </div>
    </section>
    <!-- <section class="count-card">
      <div class="card-top">
        <p class="card-intro clearfix">
          <span>7天血糖稳定率</span>
          <span>详情 <icon name="question-circle-o"></icon></span>
        </p>
        <div class="count-percent clearfix">
          <span>76%</span>
          <span>患者无血糖异常提醒</span>
        </div>
      </div>
      <div class="card-content">
        <p class="content-tags clearfix">
          <span>低血糖3人</span>
          <span>连续低血糖5人</span>
          <span>高血糖1人</span>
        </p>
      </div>
    </section> -->
    <section class="count-card patient-count">
      <div class="card-top clearfix" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <div class="top-num">
          {{TotalPatientAmount}}
        </div>
        <div class="top-right">
          <p>名患者
            <!-- <span>详情 <icon name="question-circle-o"></icon></span> -->
          </p>
          <p>近7天新增{{Last7DayNewPatientAmount}}名患者</p>
        </div>
      </div>
      <div class="card-content">
        <div class="chart-wrp">
          <div id="personRatio"></div>
        </div>
      </div>
    </section>
    <Modal title="说明" :showPop="showPop" :close="togglePop" :autoHeight="true">
      <div>
        <div class="rule">
          <p class="tag-row">
            新入组患者:12周内入组的患者
          </p>
        </div>
      </div>
    </Modal>
    <Loading :showLoading="showLoading" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import cardBg from '../../../assets/Summary/card_top_bg.png'
import Loading from '../../Shared/Loading/Loading'
import Modal from '../../Shared/Modal/Modal'
// 引入 ECharts 主模块
// const echarts = require('echarts/lib/echarts')
// 引入柱状图
// require('echarts/lib/chart/bar')
// require('echarts/lib/component/legend')
export default {
  name: 'SugarControl',
  props: ['IsReport'],
  components: {
    Loading,
    Modal
  },
  data () {
    return {
      showLoading: false,
      showPop: false,
      cardBg: cardBg,
      timeRanges: ['空腹', '早餐后', '午餐前', '午餐后', '晚餐前', '晚餐后', '睡前']
    }
  },
  computed: {
    ...mapGetters({
      passRate: 'passRate',
      sugarControl: 'sugarControl',
      PatientAgeGenderSummary: 'PatientAgeGenderSummary',
      Last7DayNewPatientAmount: 'Last7DayNewPatientAmount',
      TotalPatientAmount: 'TotalPatientAmount'
    })
  },
  methods: {
    setPersonRatio () {
      const rates = this.PatientAgeGenderSummary.rates
      const maleData = this.PatientAgeGenderSummary.maleData
      const femaleData = this.PatientAgeGenderSummary.femaleData
      const personRatioChart = echarts.init(document.getElementById('personRatio'))
      const option = {
        tooltip: {
          show: false
        },
        grid: {
          height: '90%',
          top: '10%',
          left: '20%'
        },
        legend: {
          data: ['女', '男'],
          top: '5%',
          selectedMode: false,
          right: '3%',
          icon: 'stack'
        },
        xAxis: {
          show: false
        },
        yAxis: {
          type: 'category',
          zlevel: 1,
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#D3CBCA'
            }
          },
          axisLabel: {
            textStyle: {
                color: '#000'
            }
          },
          data: ['>69岁', '60-69岁', '50-59岁', '40-49岁', '30-39岁', '<30岁']
        },
        series: [
          {
            name: '女',
            type: 'bar',
            barGap: '50%',
            barWidth: '13px',
            barCategoryGap: '50%',
            stack: '总量',
            itemStyle: {
              color: '#D3CBCA'
            },
            data: femaleData
          },
          {
            name: '男',
            type: 'bar',
            barWidth: '13px',
            stack: '总量',
            data: maleData,
            itemStyle: {
              color: '#A59A93'
            },
            label: {
              normal: {
                position: 'right',
                show: true,
                color: '#000000',
                formatter: function (params) {
                  return rates[params.dataIndex] + '%'
                }
              }
            }
          }
        ]
      }
      personRatioChart.setOption(option)
    },
    togglePop () {
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'introduction'})
      this.showPop = !this.showPop
    }
  },
  watch: {
    sugarControl: function (val, oldVal) {
      if (val.TotalPatientAmount !== undefined) {
        this.showLoading = false
      }
    },
    PatientAgeGenderSummary: function (val, oldVal) {
      this.setPersonRatio()
    }
  },
  beforeMount () {
    if (this.sugarControl.TotalPatientAmount === undefined) {
      this.showLoading = true
      this.$store.dispatch('fetchSugarControl')
    }
    if (!this.PatientAgeGenderSummary.rates[0]) {
      this.$store.dispatch('fetchPatientSum')
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



