<template>
  <div class="page-sugar">
    <section class="page-top" v-if="$route.name !== 'srSummary'">
      <div class="top-left">
        <span @click="chooseNoHcp"
          :style="{backgroundColor: currentHcpId ? '#9b9b9b' : ''}">科室平均</span>
      </div>
      <div class="depart-doctors">
        <ul class="clearfix doctors"
          :style="{width: sugarControl.DepartDctrs && sugarControl.DepartDctrs.length ? sugarControl.DepartDctrs.length * 5.6 + 'em' : ''}">
          <li class="doctor-item"
            v-for="(doctors, index) in sugarControl.DepartDctrs" :key="index">
            <span @click="selectHcp(doctors[0])"
              :class="currentHcpId === doctors[0].Item1 ? 'doctor-active' : ''">
              {{doctors[0].Item2.length > 4 ? doctors[0].Item2.slice(0, 3) + '...' : doctors[0].Item2}}
            </span>
            <br>
            <span
              @click="doctors[1] ? selectHcp(doctors[1]) : ''"
              :class="doctors[1] && (currentHcpId === doctors[1].Item1) ? 'doctor-active' : ''">
              {{doctors[1] ? (doctors[1].Item2.length > 4 ? doctors[1].Item2.slice(0, 4) + '...' : doctors[1].Item2) : ''}}
            </span>
          </li>
        </ul>
      </div>
    </section>
    <section v-if="!currentHcpId" class="pass count-card">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="card-intro clearfix">
          <span>7天血糖值达标比例</span>&nbsp;&nbsp;(总计{{sugarControl.GoodControlTotalAmount7Day}}次测量)
          <!-- <span>详情 <icon name="question-circle-o"></icon></span> -->
        </p>
        <p class="count-percent clearfix">
          <span>{{Math.round(sugarControl.GoodControlRate7Day)}}<i class="percent-icon">%</i></span>
          <span></span>
        </p>
      </div>
      <ul class="card-content">
        <li class="content-row" v-for="(item, index) in passRate" :key="index">
          <span class="time">{{timeRanges[index]}}</span>
          <p class="percent">
            <span class="percent-line" :style="{ width: (item * 0.7) + '%' }">
              <i />
            </span>
            <span class="percent-text">{{item + '%'}}</span>
          </p>
        </li>
      </ul>
    </section>
    <section v-if="!currentHcpId" class="count-card no-content">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="card-intro clearfix">
          <span>7天测量≥3次的新入组患者比例</span>
          <span class="explain" @click="togglePop">说明 <icon name="question-circle-o"></icon></span>
        </p>
        <div class="count-percent clearfix">
          <span>{{Math.round(sugarControl.NewPatientTestRate7Day)}}<i class="percent-icon">%</i></span>
          <span>
          </span>
        </div>
      </div>
    </section>
    <section v-if="currentHcpId" class="pass compare-card count-card">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="compare-intro clearfix">
          <!-- <span class="depart-counts">入组{{departTotalPatientAmount}}位患者</span> -->
          <span class="card-title">7天血糖达标比例</span>
          <!-- <span class="doctor-counts">入组{{TotalPatientAmount}}位患者</span> -->
        </p>
        <div class="card-top-wrp">
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>
                {{Math.round(sugarControl.GoodControlRate7Day)}}<i class="percent-icon">%</i>
              </span>
              <br>
              <span>总计{{sugarControl.GoodControlTotalAmount7Day}}次测量</span>
            </p>
          </div>
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>
                {{hcpControl.GoodControlRate7Day ? Math.round(hcpControl.GoodControlRate7Day) : '--'}}<i class="percent-icon">%</i>
              </span>
              <br>
              <span>总计{{hcpControl.GoodControlTotalAmount7Day}}次测量</span>
            </p>
          </div>
        </div>
      </div>
      <ul class="card-content">
        <li class="content-row" v-for="(item, index) in passRate" :key="index">
          <span class="time">{{timeRanges[index]}}</span>
          <div class="compare-percent">
            <p class="percent">
              <span class="percent-line" :style="{ width: (item * 0.7) + '%' }">
              </span>
              <span class="percent-text">{{item + '%'}}</span>
            </p>
            <p class="percent">
              <span class="percent-line" :style="{ width: (hcpPassRate[index] * 0.7) + '%' }">
                <i />
              </span>
              <span class="percent-text">{{hcpPassRate[index] + '%'}}</span>
            </p>
          </div>
        </li>
      </ul>
    </section>
    <section v-if="currentHcpId" class="compare-card count-card no-content">
      <div class="card-top" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <p class="card-intro clearfix">
          <span>7天测量≥3次的新入组患者比例</span>
        </p>
        <div class="card-top-wrp">
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>{{Math.round(sugarControl.NewPatientTestRate7Day)}}<i class="percent-icon">%</i></span>
              <span></span>
            </p>
          </div>
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>
                {{hcpControl.NewPatientTestRate7Day ? Math.round(hcpControl.NewPatientTestRate7Day) : '--'}}
                <i class="percent-icon">%</i>
              </span>
              <span></span>
            </p>
          </div>
        </div>
      </div>
    </section>
    <!-- <section class="count-card">
      <div class="card-top">
        <p class="card-intro clearfix">
          <span>7天血糖稳定率</span>
          <span>详情 <icon name="question-circle-o"></icon></span>
        </p>
        <div class="count-percent clearfix">
          <span>76%</span>
          <span>患者无血糖异常提醒</span>
        </div>
      </div>
      <div class="card-content">
        <p class="content-tags clearfix">
          <span>低血糖3人</span>
          <span>连续低血糖5人</span>
          <span>高血糖1人</span>
        </p>
      </div>
    </section> -->
    <!-- <section v-if="currentHcpId" class="compare-card count-card">
      <div class="card-top">
        <p class="card-intro clearfix">
          <span>7天血糖稳定率</span>
        </p>
        <div class="card-top-wrp">
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>{{Math.round(sugarControl.GoodControlRate7Day)}}<i class="percent-icon">%</i></span>
              <span></span>
            </p>
          </div>
          <div class="card-inner">
            <p class="count-percent clearfix">
              <span>{{Math.round(sugarControl.GoodControlRate7Day)}}<i class="percent-icon">%</i></span>
              <span></span>
            </p>
          </div>
        </div>
        <p class="card-intro clearfix">
          <span>患者无血糖异常提醒</span>
        </p>
      </div>
      <div class="card-content">
        <p class="content-text">{{Math.round(sugarControl.OldPatietnTestRate7Day)}}
          %入组超3个月的患者保持测量
          (月均测量{{Math.round(sugarControl.OldPatietnTestRate7Day)}}次)
        </p>
        <p class="content-text">{{Math.round(sugarControl.OldPatietnTestRate7Day)}}
          %入组超3个月的患者保持测量
          (月均测量{{Math.round(sugarControl.OldPatietnTestRate7Day)}}次)
        </p>
      </div>
    </section> -->
    <section class="count-card patient-count">
      <div class="card-top clearfix" :style="{backgroundImage: 'url(' + cardBg + ')'}">
        <div class="top-num">
          {{currentHcpId ? TotalPatientAmount : departTotalPatientAmount}}
        </div>
        <div class="top-right">
          <p>名患者
            <i v-if="currentHcpName">(来自 {{currentHcpName}} 医生)</i>
            <!-- <span>详情 <icon name="question-circle-o"></icon></span> -->
          </p>
          <p>近7天新增{{currentHcpId ? Last7DayNewPatientAmount : departLast7DayNewPatientAmount}}名患者</p>
        </div>
      </div>
      <div class="card-content">
        <div class="chart-wrp">
          <div id="personRatio"></div>
        </div>
      </div>
    </section>
    <Modal title="说明" :showPop="showPop" :close="togglePop" :autoHeight="true">
      <div>
        <div class="rule">
          <p class="tag-row">
            新入组患者:12周内入组的患者
          </p>
        </div>
      </div>
    </Modal>
    <Loading :showLoading="showLoading" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import cardBg from '../../../assets/Summary/card_top_bg.png'
import Loading from '../../Shared/Loading/Loading'
import Modal from '../../Shared/Modal/Modal'
// 引入 ECharts 主模块
// const echarts = require('echarts/lib/echarts')
// 引入柱状图
// require('echarts/lib/chart/bar')
// require('echarts/lib/component/legend')
export default {
  name: 'SugarControl',
  props: ['IsReport'],
  components: {
    Loading,
    Modal
  },
  data () {
    return {
      showLoading: false,
      showPop: false,
      cardBg: cardBg,
      timeRanges: ['空腹', '早餐后', '午餐前', '午餐后', '晚餐前', '晚餐后', '睡前'],
      currentHcpId: '',
      currentHcpName: ''
    }
  },
  computed: {
    ...mapGetters({
      user: 'user',
      passRate: 'passRate',
      sugarControl: 'sugarControl',
      hcpControl: 'hcpControl',
      hcpPassRate: 'hcpPassRate',
      PatientAgeGenderSummary: 'PatientAgeGenderSummary',
      Last7DayNewPatientAmount: 'Last7DayNewPatientAmount',
      TotalPatientAmount: 'TotalPatientAmount',
      departPatientAgeGenderSummary: 'departPatientAgeGenderSummary',
      departLast7DayNewPatientAmount: 'departLast7DayNewPatientAmount',
      departTotalPatientAmount: 'departTotalPatientAmount'
    })
  },
  methods: {
    selectHcp (doctor) {
      if (this.currentHcpId === doctor.Item1) {
        this.currentHcpId = ''
        this.currentHcpName = ''
      } else {
        this.currentHcpId = doctor.Item1
        this.currentHcpName = doctor.Item2 === '我' ? doctor.Item3 : doctor.Item2
        this.showLoading = true
        this.$store.dispatch('fetchHcpControl', doctor.Item1)
      }
    },
    chooseNoHcp () {
      this.currentHcpId = ''
    },
    setPersonRatio () {
      const personData = this.currentHcpId ? this.PatientAgeGenderSummary : this.departPatientAgeGenderSummary
      const rates = personData.rates
      const maleData = personData.maleData
      const femaleData = personData.femaleData
      const personRatioChart = echarts.init(document.getElementById('personRatio'))
      const option = {
        tooltip: {
          show: false
        },
        grid: {
          height: '90%',
          top: '10%',
          left: '20%'
        },
        legend: {
          data: ['女', '男'],
          top: '5%',
          right: '3%',
          selectedMode: false,
          icon: 'stack'
        },
        xAxis: {
          show: false
        },
        yAxis: {
          type: 'category',
          zlevel: 1,
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#D3CBCA'
            }
          },
          axisLabel: {
            textStyle: {
                color: '#000'
            }
          },
          data: ['>69岁', '60-69岁', '50-59岁', '40-49岁', '30-39岁', '<30岁']
        },
        series: [
          {
            name: '女',
            type: 'bar',
            barGap: '10%',
            barWidth: '13px',
            // barMinHeight: '4',
            barCategoryGap: '50%',
            stack: '总量',
            itemStyle: {
              color: '#D3CBCA'
            },
            data: femaleData
          },
          {
            name: '男',
            type: 'bar',
            barWidth: '13px',
            stack: '总量',
            data: maleData,
            itemStyle: {
              color: '#A59A93'
            },
            label: {
              normal: {
                position: 'right',
                show: true,
                color: '#000000',
                formatter: function (params) {
                  return rates[params.dataIndex] + '%'
                }
              }
            }
          }
        ]
      }
      personRatioChart.setOption(option)
    },
    togglePop () {
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'introduction'})
      this.showPop = !this.showPop
    }
  },
  watch: {
    sugarControl: function (val, oldVal) {
      if (val.TotalPatientAmount !== undefined) {
        this.showLoading = false
      }
    },
    hcpControl: function (val, oldVal) {
      this.showLoading = false
      this.setPersonRatio()
    },
    currentHcpId: function (val, oldVal) {
      if (!val) {
        this.setPersonRatio()
      }
    },
    departPatientAgeGenderSummary: function (val, oldVal) {
      this.setPersonRatio()
    }
  },
  beforeMount () {
    if (this.$route.name === 'srSummary') {
      this.showLoading = true
      this.$store.dispatch('fetchSrControl', this.$route.params.hospitalId)
    } else {
      if (this.sugarControl.TotalPatientAmount === undefined) {
        this.showLoading = true
        this.$store.dispatch('fetchSugarControl', this.user.DctrId)
      }
      if (!this.departPatientAgeGenderSummary.rates[0]) {
        this.$store.dispatch('fetchDepartPatientSum')
      }
    }
  },
  mounted () {
    if (this.departPatientAgeGenderSummary) {
      this.setPersonRatio()
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



