<template>
  <div class="page-sum">
    <div class="nav-tab" v-if="(currentTab === 'Doctors') || (currentTab !== 'SugarControl') && ($route.name !== 'srSummary') && sugarControl.TotalPatientAmount">
      <ul class="tab-wrp">
        <li v-for="(tab,index) in tabs" :key="index"
          :class="currentTab===tab.name ? 'tab-active' : ''" @click="changeTab(tab.name)">
          <span>{{tab.text}}</span></li>
      </ul>
    </div>
    <img v-if="(sugarControl.TotalPatientAmount === 0)" src="../../../assets/Summary/noSummary.png" class="noSummary" alt="">
    <component v-if="(sugarControl.TotalPatientAmount !== 0)" :is="currentTab" keep-alive></component>
    <Footer />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import Footer from '../../Shared/Footer/Footer'
import SugarControl from './SugarControl.vue'
import DepartControl from './DepartControl.vue'
import Doctors from './Doctors.vue'
export default {
  name: 'Summary',
  components: {
    Footer,
    SugarControl,
    DepartControl,
    Doctors
  },
  data () {
    return {
      currentTab: '',
      tabs: []
    }
  },
  computed: {
    ...mapGetters({
      user: 'user',
      sugarControl: 'sugarControl'
    })
  },
  methods: {
    changeTab: function (tab) {
      this.currentTab = tab
      this.$router.push(`/summary/${tab}`)
    }
  },
  beforeMount: function () {
    if (this.$route.name === 'srSummary') {
      this.tabs = [{
        name: 'DepartControl',
        text: '科室糖控'
      }]
      this.currentTab = 'DepartControl'
    } else {
      if (this.user.IsReport) {
        this.tabs = [{
          name: 'DepartControl',
          text: '科室糖控'
        },
        {
          name: 'Doctors',
          text: '医生详情'
        }]
        this.currentTab = 'DepartControl'
      } else {
        this.tabs = [{
          name: 'SugarControl',
          text: '控糖情况'
        }]
        this.currentTab = 'SugarControl'
      }
      if (this.$route.params.tab === 'Doctors') {
        this.changeTab('Doctors')
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";
</style>



