<template>
  <div class="page-doctors">
    <div class="doctor-list">
      <ul class="list-header">
        <li v-for="(date, index) in dates" :key="index"
        @click="fetchDoctorList(date.dateRange)"
        :class="currentRange === date.dateRange ? 'filter-active' : ''">{{date.text}}</li>
      </ul>
      <ul class="list-wrp">
        <li class="list-sort list-row">
          <ul>
            <li>医生名</li>
            <li @click="changeFilter('Sum')"
            :class="currentFilter === 'Sum' ? 'filter-active' : ''">
              患者总数
              <span class="sort-icon-asc" :class="currentSort==='asc'?'icon-active':''">
                <icon name="sort-asc" scale="0.7"></icon>
              </span>
              <span class="sort-icon-desc" :class="currentSort==='desc'?'icon-active':''">
                <icon name="sort-desc" scale="0.7"></icon>
              </span>
            </li>
            <!-- <li class="mini-line"  @click="changeFilter('normalNum')"
            :class="currentFilter === 'normalNum' ? 'filter-active' : ''">
              无异常提醒<br>患者数
              <span class="sort-icon-asc" :class="currentSort==='asc'?'icon-active':''">
                <icon name="sort-asc" scale="0.7"></icon>
              </span>
              <span class="sort-icon-desc" :class="currentSort==='desc'?'icon-active':''">
                <icon name="sort-desc" scale="0.7"></icon>
              </span>
            </li> -->
            <li class="mini-line" @click="changeFilter('BGCount')"
            :class="currentFilter === 'BGCount' ? 'filter-active' : ''">
              测量≥3次<br>患者数
              <span class="sort-icon-asc" :class="currentSort==='asc'?'icon-active':''">
                <icon name="sort-asc" scale="0.7"></icon>
              </span>
              <span class="sort-icon-desc" :class="currentSort==='desc'?'icon-active':''">
                <icon name="sort-desc" scale="0.7"></icon>
              </span>
            </li>
          </ul>
        </li>
        <li class="list-row"
          v-for="doctor in (sortDoctors.length ? sortDoctors : doctors)"
          @click="goToList(doctor)"
          :key="doctor.id">
          <ul>
            <li>{{doctor.DoctorName}}</li>
            <li>{{doctor.Sum}}</li>
            <!-- <li>{{doctor.normalNum}}</li> -->
            <li>
              {{doctor.BGCount}}
              <span class="link-icon">
                <icon name="angle-right" scale="1.5"></icon>
              </span>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <Loading :showLoading="sendStatus" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import Loading from '../../Shared/Loading/Loading'
export default {
  name: 'Doctors',
  props: ['role'],
  components: {
    Loading
  },
  data () {
    return {
      sortDoctors: [],
      currentRange: 0,
      showLoading: false,
      currentSort: 'desc',
      currentFilter: 'BGCount',
      dates: [
        {
          text: '近7天',
          dateRange: 0
        },
        {
          text: '近28天',
          dateRange: 1
        },
        {
          text: '近12周',
          dateRange: 2
        }
      ]
    }
  },
  computed: {
    ...mapGetters({
      doctors: 'doctors',
      sendStatus: 'sendStatus',
      user: 'user'
    })
  },
  methods: {
    sortList (a, b) {
      let result = this.currentSort === 'desc' ? b[this.currentFilter] - a[this.currentFilter] : a[this.currentFilter] - b[this.currentFilter]
      if (result !== 0) {
        return result
      } else {
        return a.DoctorName.localeCompare(b.DoctorName, 'zh-CN')
      }
    },
    changeFilter (rule) {
      if (rule === this.currentFilter) {
        this.currentSort = this.currentSort === 'asc' ? 'desc' : 'asc'
      } else {
        this.currentSort = 'desc'
      }
      this.currentFilter = rule
      this.sortDoctors = this.doctors.sort(this.sortList)
    },
    fetchDoctorList (time) {
      this.$store.dispatch('fetchDoctorList', time)
      this.currentRange = time
      this.sortDoctors = this.doctors.sort(this.sortList)
    },
    goToList (doctor) {
      if (doctor.HcpId !== this.user.HcpId) {
        this.$store.dispatch('departWatchDoctor', doctor)
        this.$router.push('/depart-patient-list/' + doctor.HcpId)
      } else {
        this.$router.push('/patient-list/default')
      }
    }
  },
  watch: {
    sendStatus: function (val, oldVal) {
      if (!val) {
        this.currentSort = 'desc'
        this.currentFilter = 'BGCount'
      }
    }
  },
  beforeMount () {
    if (!this.doctors.length) {
      this.fetchDoctorList(0)
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



